@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2"), url("../fonts/opensans.woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2"), url("../fonts/opensansbold.woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

/* Fonts */
/* Breakpoints */
/* Colors */
.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}

/* Fonts */
/* Breakpoints */
/* Colors */
.container {
  width: 100%;
  max-width: 1150px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}

.button {
  padding: 15px 15px 13px 15px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #444444;
  background-color: transparent;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}

.title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

@media (min-width: 768px) {
  .main-nav {
    display: grid;
    grid-template-columns: 48% 52%;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 1fr 149px 1fr;
  }
}

.main-nav__wrapper-top {
  position: relative;
  display: flex;
  align-items: center;
  width: auto;
  margin: 0 -30px;
  padding: 36px 0 31px 0;
}
.main-nav__wrapper-top::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  height: 1px;
  width: 100%;
  background-color: #ececec;
}
@media (min-width: 768px) {
  .main-nav__wrapper-top {
    margin: 0;
    padding: 22px 0 16px 0;
  }
  .main-nav__wrapper-top::after {
    width: 0;
    height: 0;
  }
}
@media (min-width: 1150px) {
  .main-nav__wrapper-top {
    grid-column: 2/3;
    grid-row: 1/2;
    margin: 0;
    padding: 9px 0 3px 0;
  }
}

.main-nav__logo {
  cursor: pointer;
  transition: all 0.3s ease;
}
@media (max-width: 767px) {
  .main-nav__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    transform: translate(-50%, -50%);
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-nav__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-img {
    width: 149px;
    height: 72px;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    position: absolute;
    left: 0;
    right: 0;
    display: none;
    background-color: #ffffff;
    z-index: 3;
  }
}

.main-nav__item {
  position: relative;
  padding: 28px 30px 27px 76px;
}
@media (max-width: 767px) {
  .main-nav__item::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    height: 1px;
    width: 100%;
    background-color: #ececec;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    max-width: 265px;
  }
}

.main-nav__link {
  display: block;
  height: 100%;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  transition: all 0.3s ease;
}
.main-nav__link[href]:hover {
  opacity: 0.6;
}
.main-nav__link[href]:active {
  opacity: 0.3;
}

.main-nav__toggle {
  position: relative;
  margin: 0 30px 0 auto;
  width: 20px;
  height: 14px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:disabled {
  opacity: 0.3;
}

.main-nav__toggle-line {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #231f20;
  transition: 0.3s ease;
}
.main-nav__toggle-line:nth-child(1) {
  top: -4px;
}
.main-nav__toggle-line:nth-child(2) {
  top: 2px;
}
.main-nav__toggle-line:nth-child(3) {
  top: 2px;
}
.main-nav__toggle-line:nth-child(4) {
  top: 8px;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list:not(:last-of-type) {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle-line:nth-child(1) {
  transform: scaleX(0);
}
.main-nav--opened .main-nav__toggle-line:nth-child(2) {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-line:nth-child(3) {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle-line:nth-child(4) {
  transform: scaleX(0);
}

.extra-nav {
  display: none;
}
@media (min-width: 768px) {
  .extra-nav {
    display: flex;
    grid-column: 2/-1;
    justify-self: end;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .extra-nav {
    grid-column: 1/-1;
    width: 100%;
  }
}

.extra-nav__item {
  padding: 21px 0;
}
@media (min-width: 768px) {
  .extra-nav__item:not(:last-child) {
    display: none;
  }
}
@media (min-width: 1150px) {
  .extra-nav__item:not(:last-child) {
    display: block;
    margin: 0 62px 0 0;
  }
}
@media (min-width: 1150px) {
  .extra-nav__item:last-child {
    margin: 0 0 0 auto;
  }
}

@media (min-width: 768px) {
  .extra-nav__text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    color: #444444;
  }
}

@media (min-width: 1150px) {
  .extra-nav__link {
    font-size: 15px;
  }
}

.user-nav {
  top: 48%;
}
@media (min-width: 768px) {
  .user-nav {
    position: relative;
    top: unset;
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
    grid-column: 2/-1;
    grid-row: 1/2;
    width: auto;
    margin: 0 -50px 0 0;
    padding: 0 50px 0 0;
  }
}
@media (min-width: 1150px) {
  .user-nav {
    grid-column: 3/-1;
    padding: 0 52px 0 0;
  }
}

.user-nav__item {
  padding: 28px 30px 27px 30px;
}
@media (min-width: 768px) {
  .user-nav__item {
    padding: 29px 32px 27px 32px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .user-nav__item:last-child {
    padding: 29px 0 27px 32px;
  }
}

.user-nav__link {
  display: flex;
  align-items: center;
}
.user-nav__link:hover .user-nav__icon {
  color: #63d1bb;
  opacity: 1;
}

.user-nav__icon {
  margin: 0 28px 0 0;
  color: #000000;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .user-nav__icon {
    margin: 0 32px 0 0;
  }
  .user-nav__icon--hidden-text {
    margin: 0;
  }
}

@media (min-width: 768px) {
  .user-nav__text {
    display: none;
  }
}

@media (min-width: 768px) {
  .site-nav {
    display: flex;
    flex-wrap: wrap;
    grid-column: 1/2;
    width: 100%;
    background-color: transparent;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .site-nav {
    margin: 0 0 0 -35px;
  }
}

@media (min-width: 768px) {
  .site-nav__item {
    max-width: 230px;
    padding: 21px 0;
  }
}
@media (min-width: 768px) {
  .site-nav__item:not(:last-child) {
    margin: 0 36px 0 0;
  }
}
@media (min-width: 1150px) {
  .site-nav__item:not(:last-child) {
    margin: 0;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 1150px) {
  .site-nav__item {
    padding: 33px 35px 31px 35px;
  }
}

@media (min-width: 768px) {
  .site-nav__link {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .site-nav__link {
    font-size: 17px;
  }
}

@media (min-width: 768px) {
  .hero {
    position: relative;
    margin: -66px 0 0 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
  .hero::after {
    content: "";
    position: absolute;
    bottom: -6px;
    width: 100%;
    height: 24px;
    background-image: url("../img/sprite.svg#hero-mask-tablet");
    background-repeat: repeat-x;
    background-position: center;
    z-index: 4;
  }
}
@media (min-width: 870px) {
  .hero::after {
    height: 34px;
    bottom: -11px;
    background-image: url("../img/sprite.svg#hero-mask-desktop");
  }
}

.hero__wrapper {
  padding: 70px 0 193px 0;
  background-color: #f1f1f1;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: top -20px center;
  background-size: 100% auto;
}
@media (min-resolution: 2dppx) {
  .hero__wrapper {
    background-image: url("../img/hero-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero__wrapper {
    padding: 154px 0 491px 0;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: top 0 center;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero__wrapper {
    background-image: url("../img/hero-tablet@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .hero__wrapper {
    padding: 158px 0 474px 0;
    background-image: url("../img/hero-desktop@1x.jpg");
    background-size: cover;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .hero__wrapper {
    background-image: url("../img/hero-desktop@2x.jpg");
  }
}

.hero__title {
  margin: 0;
  padding: 0 30px;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__title {
    max-width: 668px;
    margin: 0 auto;
    padding: 0 50px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    max-width: 720px;
    font-size: 53px;
    line-height: 57px;
  }
}

@media (min-width: 768px) {
  .hero__break {
    display: none;
  }
}

.hero__list {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: -72px 0 0 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .hero__list {
    flex-direction: row;
    flex-wrap: wrap;
    margin: -119px 0 0 0;
  }
}
@media (min-width: 1150px) {
  .hero__list {
    margin: -120px 0 0 0;
  }
}

.hero__item {
  position: relative;
  width: auto;
  margin: 0 -30px;
  transition: all 0.3s ease;
}
.hero__item:hover {
  transform: translateY(-5px);
}
.hero__item:active {
  transform: translateY(0);
}
.hero__item:nth-child(odd) {
  background-color: #63d1bb;
}
.hero__item:nth-child(odd):hover {
  background-color: #59d5bc;
}
.hero__item:nth-child(odd):active {
  background-color: #59d5bc;
}
.hero__item:nth-child(even) {
  background-color: #56c3b2;
}
.hero__item:nth-child(even):hover {
  background-color: #46c1ae;
}
.hero__item:nth-child(even):active {
  background-color: #46c1ae;
}
.hero__item::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.hero__item--interior::after {
  right: 36px;
  width: 40px;
  height: 62px;
  background-image: url("../img/sprite.svg#icon-interior");
}
.hero__item--toy::after {
  right: 27px;
  width: 60px;
  height: 60px;
  background-image: url("../img/sprite.svg#icon-toy");
}
@media (min-width: 768px) {
  .hero__item--toy::after {
    right: 33px;
  }
}
@media (min-width: 768px) {
  .hero__item {
    width: 50%;
    margin: 0;
  }
}

.hero__link {
  display: block;
  width: 100%;
  height: 100%;
  padding: 24px 160px 31px 30px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .hero__link {
    padding: 25px 160px 40px 36px;
    font-size: 20px;
    line-height: 30px;
  }
}

.hero__text {
  display: block;
  max-width: 130px;
}

.promo {
  position: relative;
  padding: 0 0 43px 0;
}
.promo::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 7px;
  background-image: url("../../img/sprite.svg#zigzag-line");
  background-repeat: repeat-x;
}
@media (min-width: 768px) {
  .promo {
    padding: 0 0 70px 0;
  }
  .promo::after {
    height: 17px;
  }
}
@media (min-width: 1150px) {
  .promo {
    padding: 0 0 95px 0;
  }
  .promo::after {
    height: 23px;
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  margin: 31px 0 0 0;
}
@media (min-width: 768px) {
  .promo__wrapper {
    display: grid;
    grid-template-columns: 43.7% 50%;
    column-gap: 42px;
    margin: 46px 0 0 0;
  }
}
@media (min-width: 1150px) {
  .promo__wrapper {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 0;
    margin: 54px 0 0 0;
  }
}

.promo__header {
  order: 1;
}
@media (min-width: 768px) {
  .promo__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-column: 1/-1;
    grid-row: 1/2;
    margin: 0 0 38px 0;
  }
}
@media (min-width: 1150px) {
  .promo__header {
    grid-column: 1/5;
    grid-row: 1/-1;
    margin: 70px 0 38px 31px;
  }
}

.promo__title {
  margin: 0 0 5px 0;
  z-index: 2;
}
@media (min-width: 768px) {
  .promo__title {
    margin: 0;
  }
}

.promo__lead {
  margin: 0 0 16px 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63d1bb;
}
@media (min-width: 768px) {
  .promo__lead {
    max-width: 100px;
    margin: -4px 14px 0 0;
    text-align: center;
    line-height: 17px;
  }
}
@media (min-width: 1150px) {
  .promo__lead {
    order: -1;
    margin: -4px 30px 0 0;
  }
}

.promo__description {
  order: 2;
}
@media (min-width: 1150px) {
  .promo__description {
    grid-column: 2/4;
    grid-row: 2/3;
    max-width: 292px;
  }
}

.promo__description-text {
  margin: 0 0 28px 0;
  font-size: 15px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .promo__description-text {
    margin: 0 0 48px 0;
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .promo__description-text {
    margin: 0 0 59px 0;
  }
}

.promo__info {
  order: 3;
  margin: 0 0 28px 0;
  font-size: 15px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .promo__info {
    margin: 0 0 57px 0;
  }
}
@media (min-width: 1150px) {
  .promo__info {
    margin: 0 0 109px 0;
  }
}

.promo__info-wrapper {
  display: grid;
  grid-template-columns: 90px 1fr;
  column-gap: 7px;
  margin: 0 0 7px 0;
  padding: 0 0 12px 0;
  border-bottom: 1px solid #ececec;
}

.promo__info-def {
  margin: 0;
}

.promo__picture {
  order: 4;
  margin: 0 0 27px 0;
}
@media (min-width: 768px) {
  .promo__picture {
    order: 5;
    grid-column: 2/-1;
    grid-row: 2/4;
    margin: 12px 0 0 0;
  }
}
@media (min-width: 1150px) {
  .promo__picture {
    grid-column: 4/-1;
    grid-row: 1/4;
  }
}

.promo__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.promo__buy {
  display: flex;
  flex-direction: column;
  order: 5;
}
@media (min-width: 768px) {
  .promo__buy {
    order: 4;
    grid-column: 1/2;
  }
}
@media (min-width: 1150px) {
  .promo__buy {
    grid-column: 2/4;
    grid-row: 3/4;
    max-width: 292px;
  }
}

.promo__price {
  margin: 0 auto 23px auto;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__price {
    margin: 0 0 32px 0;
    font-size: 20px;
  }
}

.promo__button {
  width: 100%;
}